<i18n>
{
  "en": {
    "language": "Language"
  },
  "zh": {
    "language": "多语言设置"
  }
}
</i18n>

<template>
  <div class="about">
    <head-top :title='$t("language")' :go-back="true"></head-top>
    <div class="about-container">
      <ul class="list">
        <li @click="itemHandle(1)">
          中文
          <img v-show="active===1" src="../../assets/global_images/click@3x.png" alt="">
          <div class="bomPixel"></div>
        </li>
        <li @click="itemHandle(2)">
          English
          <img v-show="active===2" src="../../assets/global_images/click@3x.png" alt="">
          <div class="bomPixel"></div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import headTop from '../../components/Header/index'
import { mapActions } from 'vuex'
export default {
  name: 'about',
  data () {
    return {
      active: 1 // 1 zh、 2 en
    }
  },

  components: {
    headTop
  },

  mounted () {
    if (this.$i18n.locale === 'zh') {
      this.active = 1
    } else {
      this.active = 2
    }
  },

  methods: {
    ...mapActions([
      'setLanguage'
    ]),

    itemHandle (num) {
      switch (num) {
        case 1:
          // this.setLanguage('zh')
          // 设置系统语言请求
          this.dataInteractionRequest({
            msgType: '0x5042',
            language: 'zh'
          })
          break
        case 2:
          // this.setLanguage('en')
          // 设置系统语言请求
          this.dataInteractionRequest({
            msgType: '0x5042',
            language: 'en'
          })
          break
      }
      this.active = num
      this.$router.back()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.about{
  .fs;
  background: #FFF;
  .about-container{
    .fs-noTop;
    .list{
      .wh(100%, auto);
      position: relative;
      li{
        .wh-l(100%, 90px);
        font-size: 30px;
        padding: 0 36px;
        text-align: left;
        position: relative;
        img{
          height: 30px;
          position: absolute;
          top: 50%;
          right: 36px;
          margin-top: -15px;
        }
      }
    }
  }
}
</style>



